import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home'
import Create from './views/Create'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      meta: {layout: 'main'},
      component: Home
    },
    {
      path: '/create',
      name: 'create',
      meta: {layout: 'empty'},
      component: Create
    }
  ]
})