<template>
  <div class="container">
    <h3>{{card.title}}</h3>
    <p>{{description}}</p>
    <button @click="showCard">подробнее</button>
  </div>
</template>

<script>
import {mapActions} from 'vuex'
export default {
  name: 'card',
  props: ['card'],
  computed: {
    isShortDesc() {
      return this.card.desc ? this.card.desc.length < 25 : true;
    },
    description() {
      return this.isShortDesc ? this.card.desc : this.card.desc.substr(0, 25) + '...'
    }
  },
  methods: {
    ...mapActions('modal', ['update']),
    showCard() {
      this.update(this.card)
    }
  }
}
</script>

<style scoped>
  h3 {
   margin: 5px 
  }
  .container {
    box-shadow: 0 0 5px rgba(0,0,0,0.3);
  }
</style>