export default {
  namespaced: true,
  state: {
    title: null,
    desc: null,
    createdAt: null,
    isShow: false
  },
  actions: {
    close: ({commit}) => commit('toggle', false),
    open: ({commit}) => commit('toggle', true),
    update: ({commit}, data) => {
      commit('update', data)
    }
  },
  mutations: {
    toggle: (state, data) => state.isShow = data,
    update: (state, data) => {
      state.title = data.title,
      state.desc = data.desc,
      state.createdAt = data.createdAt
      state.isShow = true
    }
  },
  getters: {
    isShow: s => s.isShow,
    title: s => s.title,
    desc: s => s.desc,
    createdAt: s => s.createdAt
  }
}