import Vue from 'vue'
import Vuex from 'vuex'
import records from './records'
import modal from './modal'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    dnd: true
  },
  actions: {
    toggle({commit}, value) {
      commit('updateDragAndDrop', value)
    }
  },
  mutations: {
    updateDragAndDrop: (state, value) => state.dnd = value
  },
  getters: {
    dragAndDrop: s => s.dnd
  },
  modules: {records, modal}
})