<template>
  <nav>
    <label>
      <input v-model="toggleDnD" type="checkbox" />
      drag&drop
    </label>
    <button @click="addRecord">добавить</button>
  </nav>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'
export default {
  name: 'navbar',
  computed: {
    ...mapGetters(['dragAndDrop']),
    toggleDnD: {
      get() {
        return this.dragAndDrop
      },
      set(value) {
        this.toggle(value)
      }
    }
  },
  methods: {
    ...mapActions(['toggle']),
    addRecord() {
      this.$router.push('/create')
    }
  }
}
</script>

<style scoped>
  nav {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    padding: 5px;
  }
</style>