<template>
    <draggable class="row" v-model="records" :disabled="!dragAndDrop">
      <Card 
        v-for="item in records" 
        :key="item.id"
        :card="item"
        class="card"
      />
    </draggable>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'
import Card from '../components/Card'
import draggable from 'vuedraggable'

export default {
  name: 'home',
  computed: {
    ...mapGetters('records', ['list']),
    ...mapGetters(['dragAndDrop']),
    records: {
      get() {
        return this.list;
      },
      set(value) {
        this.sort(value)
      }
    }
  },
  components: {Card, draggable},
  methods: {
    ...mapActions('records', ['sort'])
  }
}
</script>

<style scoped>
  .row {
    display: flex;
    flex-wrap: wrap;
    background-color: #FFF
  }
  .card {
    padding: 5px;
    flex-basis: 300px;
    max-width: 300px;
    margin: 15px 5px;
  }
</style>