<template>
  <div class="container">
    <form>
      <input v-model="title" type="text">
      <textarea v-model="desc" cols="30" rows="5"></textarea>
    </form>
    <div>
      <button :disabled="isValid" @click="save">Добавить</button>
      <button @click="back">Отмена</button>
    </div>
  </div>
</template>

<script>
import {mapActions} from 'vuex'
export default {
  name: 'create',
  data: () => ({
    title: null,
    desc: null
  }),
  computed: {
    isValid() {
      return !this.title || !this.desc
    }
  },
  methods: {
    ...mapActions('records', ['add']),
    save() {
      this.add({title: this.title, desc: this.desc})
      this.back()
    },
    back() {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped>
  form {
    display: flex;
    flex-direction: column;
    padding: 10px
  }
 .container {
   box-shadow: 0 0 5px rgba(0,0,0,0.3)
 }
</style>